body {
  text-align: center;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

*, :before, :after {
  box-sizing: border-box;
}

h1 {
  color: #fff;
  background-color: #1c5a8d;
  margin-top: 0;
  padding: 10px;
}

.container {
  max-width: 400px;
  margin: 20px auto;
}

.form-title {
  margin-bottom: .25rem;
}

.form-container {
  flex-direction: column;
  margin-bottom: 20px;
  display: flex;
}

ul {
  padding: 0;
  list-style: none;
}

label {
  text-align: left;
  margin-bottom: 6px;
  font-weight: bold;
}

input[type="text"], select {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin: 0 0 20px;
  padding: 10px;
  transition: box-shadow .3s, border-color .3s;
  box-shadow: 0 2px 4px #0000001a, 0 4px 8px #0000000d;
}

input[type="text"]:focus, select:focus, input[type="text"]:hover, select:hover {
  border-color: #1c5a8d;
  outline: none;
  box-shadow: 0 2px 6px #1c5a8d4d, 0 6px 12px #1c5a8d26;
}

input::placeholder {
  color: #595959;
}

.add-book-btn {
  font-size: 1.2rem;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #1c5a8d;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
}

button:hover {
  background-color: #023d77;
}

#bookList li {
  background-color: #fcfcfc;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-bottom: 10px;
  padding: 15px 20px;
  list-style: none;
  box-shadow: 0 2px 5px #0000001a;
}

.book-header {
  justify-content: flex-start;
  align-items: baseline;
  gap: 1rem;
  margin-bottom: 5px;
  display: flex;
}

.book-title {
  margin: 0;
  font-size: 1.5rem;
  font-weight: bold;
}

.book-author {
  color: #444;
  margin: 0;
  font-size: 1rem;
}

.book-genre {
  text-align: left;
  margin: 5px 0;
  font-size: .9rem;
  font-style: italic;
}

.book-rating {
  margin: 8px 0;
}

.book-rating.read {
  font-size: 1.2rem;
  font-weight: bold;
}

.book-rating.not-read {
  font-size: 1.05rem;
  font-style: italic;
}

.book-rating-input {
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 8px;
  display: flex;
}

.book-rating-input input[type="number"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 50px;
  margin-right: 5px;
  padding: 8px;
}

#chatbot-container {
  background: #fff;
  border: 1px solid #ccc;
  width: 300px;
  padding: 10px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

#chat-history {
  border-bottom: 1px solid #ccc;
  height: 200px;
  margin-bottom: 10px;
  padding: 5px;
  overflow-y: auto;
}

#chat-input {
  width: calc(100% - 60px);
  margin-right: 5px;
  padding: 5px;
}

#send-btn {
  cursor: pointer;
  padding: 5px 10px;
}

.history {
  padding-bottom: 15px;
}
